<template>
	<view class="myOrder">
		<view class="tabs">
			<view class="tabs_wrap">
				<view :class="item.id==thisID?'tabs_item':''" v-for="item in tabs" :key="item.id" @click="handelChild(item)">{{item.name}}</view>
			</view>
		</view>
		<!-- <tabs :tabs="tabs" @handelChild="handelChild"></tabs> -->
		<view class="allOrder_container">
			<view @click="handleDetail(item)" class="allOrder_item" v-for="(item,index) in orderList" :key="index">
				<view class="allOrder_title">
					<view class="allOrder_title_left"><!-- '福彩到家'+ -->
						<text>{{item.orde.Self_Delivery}}</text>{{!!item.orde.OutletsName?(item.orde.OutletsName+'投注站'):''}}
						<!-- <text>{{item.orde.Self_Delivery}}</text>{{item.orde.Self_Delivery == '外送'?item.orde.Address:(item.orde.OutletsName+'投注站')}} -->
					</view>
					<view class="allOrder_title_right" v-if="item.orde.OrderState!=1">{{item.orde.name}}</view>
					<view class="jiesuan_warp" v-if="item.orde.OrderState==1">
					<view class="jiesuan delete" @click="handleSettle(item.orde)">结算</view>
					<view class="jiesuan" @click="handleDelete(item.orde)">删除</view>
					</view>
				</view>
				<view class="allOrder_list">
					<!-- 彩票内容开始 -->
					<view class="allOrder_item">
						<view class="allOrder_item_icon">
							<image :src="item.orde.icon" mode="widthFix"></image>
						</view>
						<view class="allOrder_item_wrap">
							<view class="allOrder_item_info" v-for="(redItem,redindex) in item.list" :key="redindex">
							<!-- 双色球、七乐彩、福彩3D -->
								<view class="qiu_text" v-if="item.orde.LotteryType<4">
									<!-- #ifdef MP-WEIXIN -->
									<text v-for="red in redItem.RedBall" :key="red">{{red}}{{item.orde.LotteryType==4?' * '+(redItem.Price/red)+'元':''}}</text><text class="blue" v-for="blue in redItem.BlueBall"
									<!-- #endif -->
									<!-- #ifdef H5 -->
									<text v-for="(red,i) in redItem.RedBall" :key="i">{{red}}{{item.orde.LotteryType==4?' * '+(redItem.Price/red)+'元':''}}</text><text class="blue" v-for="blue in redItem.BlueBall"
									 :key="blue">{{blue}}</text>
									<!-- #endif -->
									 
									 <!-- <view class="qiu_text_item">第{{redItem.Number}}期</view> -->
									 
								</view>
								<!-- 刮刮乐 -->
								<view class="qiu_text" v-else>
									<text>{{redItem.ProductName}} {{redItem.Price}}福豆/张</text>
								</view>
								<!-- 双色球、七乐彩、福彩3D -->
								<view class="allOrder_item_qishu" v-if="item.orde.LotteryType<4">
									<view class="qishu_text">
										<!-- <text>期数*{{redItem.Fixed}}</text> -->
										<text>期数*{{redItem.Num}}</text>
										<text>倍数*{{redItem.Multiple}}</text>
									</view>
									<view class="qishu_info">
										第{{redItem.Fixed}}期
									</view>
								</view>
								<!-- 刮刮乐 -->
								<view class="allOrder_item_qishu" v-else>
									<view class="qishu_text">
										<text>共计：{{redItem.Num}} 张</text>
									</view>
								</view>
							</view>

						</view>
					</view>
					<!-- 彩票内容结束 -->
					<view class="total_wrap">
						<view class="total_time">{{item.orde.AddTime}}</view>
						<view class="total_right">
							<text>共 {{item.list.length}} 组，合计：{{item.orde.Price}} 福豆</text>
							<text v-if="item.orde.DeductionAmount>0">(体验卷:-{{item.orde.DeductionAmount}})</text>
						</view>
					</view>
				</view>
			</view>
		</view>
	</view>
</template>
<script>
	// import tabs from '../../components/tabs/tabs';
	import common from '../../common/common';
	var _self;
	export default {
		// components: {
		// 	tabs
		// },
		data() {
			return {
				tabs: [{
						id: 0,
						name: '全部',
						param: '111',
						isActive: true
					},
					{
						id: 1,
						name: '待确定',// 原 待结算
						param: '222',
						isActive: false
					},
					{
						id: 2,
						name: '兑票中',	// 原 待出票
						param: '333',
						isActive: false
					},
					{
						id: 3,
						name: '待领取',
						param: '4444',
						isActive: false
					},
					{
						id: 4,
						name: '已完成',
						param: '555',
						isActive: false
					}
					// ,
					// {
					// 	id: 6,
					// 	name: '退单中',
					// 	param: '777',
					// 	isActive: false
					// },
					// {
					// 	id: 5,
					// 	name: '已退单',
					// 	param: '666',
					// 	isActive: false
					// }
				],
				orderstate: "",
				orderList: [],
				active: false,
				state: {},
				thisID: 0,
			}
		},
		onLoad(e) {
			_self = this;
			if (!!e.id) {
				_self.thisID = e.id;
			}
			_self.state = common.getstate();
			_self.handleOeder();
			
			let pages = getCurrentPages();//获取页面栈
			let prevPage = pages[pages.length - 2] // 获取上一级页面
			if(prevPage.canClear === false){
				prevPage.canClear = true;
			}
			
		},
		onShow() {
			_self.handleOeder();
			// let page = getCurrentPages();
			// _self.orderstate = page[page.length - 1].options.id;
			// if (_self.orderstate != "") {
			// 	_self.tabs.forEach((element, index) => {
			// 		index == _self.orderstate ? element.isActive = true : element.isActive = false;
			// 	});
			// } else {
			// 	_self.tabs.forEach((element, index) => {
			// 		index == 0 ? element.isActive = true : element.isActive = false
			// 	});
			// }

		},
		methods: {
			// 点击跳转详情页
			handleDetail(_item) {
				// uni.setStorageSync('list', _self.orderList[i].list);
				// uni.setStorageSync('detail', _self.orderList[i].orde);
				if (_item.orde.OrderState == 2 || _item.orde.OrderState == 5||_item.orde.OrderState == 6) {
					uni.navigateTo({
						url: '../mine/waitTicket?item=' + encodeURIComponent(JSON.stringify(_item)), //待出票
					});
				} else if (_item.orde.OrderState == 3) {
					uni.navigateTo({
						url: '../mine/waitRecive?item=' + encodeURIComponent(JSON.stringify(_item)), //待领取
					});
				} else if (_item.orde.OrderState == 4) {
					uni.navigateTo({
						url: '../mine/wancheng?item=' + encodeURIComponent(JSON.stringify(_item)), //已完成
					});
				}
			},
			handelChild(_item) {
				_self.thisID = _item.id;
				_self.handleOeder();
			},
			handleOeder() {
				common.request({
					path: "Order/getpage",
					data: {
						memerid: _self.state.ID,
						orderstate: _self.thisID > 0 ? _self.thisID : '',
						rows: 50,
						page: 1,
						sidx: "AddTime",
						sord: "desc"
					},
					success: res => {
						res.data.rows.forEach(elem => {
							elem.list.forEach(element => {
								
								if (element.RedBall != null) {
									element.RedBall = element.RedBall.split(",");
								}
								if (element.BlueBall != null) {
									element.BlueBall = element.BlueBall.split(",");
								}
							})
							if (elem.orde.OrderState == 1) {
								elem.orde.name = "待确定"
							} else if (elem.orde.OrderState == 2) {
								elem.orde.name = "兑换中"
							}else if (elem.orde.OrderState == 3) {
								elem.orde.name = "待领取"
							} else if (elem.orde.OrderState == 4) {
								elem.orde.name = "已完成"
							} else if (elem.orde.OrderState == 5) {
							 	elem.orde.name = "已退单"
							} else if (elem.orde.OrderState == 6) {
								elem.orde.name = "审核中"
							}
							if (elem.orde.Self_Delivery == 1) {
								elem.orde.Self_Delivery = "自提"
							} else if (elem.orde.Self_Delivery == 2) {
								elem.orde.Self_Delivery = "到家"
							} else if (elem.orde.Self_Delivery == 3) {
								elem.orde.Self_Delivery = "托管"
							}
							if (elem.orde.LotteryType == "1") {
								elem.orde.icon = "https://fucai.yiruit.com/UpFiles/mine/shuangseqiu.png"
							} else if (elem.orde.LotteryType == "2") {
								elem.orde.icon = "https://fucai.yiruit.com/UpFiles/mine/fucai3d.png"
							} else if (elem.orde.LotteryType == "3") {
								elem.orde.icon = "https://fucai.yiruit.com/UpFiles/mine/qilecai.png"
							}else if (elem.orde.LotteryType == "4") {
								elem.orde.icon = "http://fucai.yiruit.com/UpFiles/tupian/le.png"
							}
						});
						
						_self.orderList = res.data.rows;
					}
				});
			},
			// 待结算订单点击删除按钮
			handleDelete(item){
				uni.showModal({
					title:'删除订单',
					content:'请问是否确认删除当前订单',
					success(e) {
						if(e.confirm){
							common.request({
								path:'Order/deleteorder',
								data:item.ID,
								success(){
									_self.handleOeder();
									uni.showModal({
										title:'完成',
										content:'您的订单已删除',
										showCancel:false
									})
								}
							})
						}else if(e.cancel){
							return false;
						}
					}
				})
			},
			// 待结算订单点击结算按钮
			handleSettle(_order) {
				if (_self.state.Balance < _order.Price) {

					uni.showModal({
						cancelText: '获取福豆',
						title: '您的福豆不足',
						content: '请至线下福彩到家网点获取福豆',
						success(res) {

						}
					});
					//return false;
				} else {
					uni.showModal({
						showCancel: false,
						title: '确定结算？',
						content: '您的余额为：￥' + _self.state.Balance,
						success(res) {
							if (res.confirm) {
								common.request({
									path: "Order/jiesuan",
									data: _order.ID,
									success: res => {
										_self.state.Balance = _self.state.Balance - _order.Price;
										common.setstate(_self.state);
										_self.handleOeder();
										uni.showToast({
											title:'提交成功',
											icon:'none'
										})
									}
								});
							}
						}
					});
				}
			}
		},
		watch: {
			orderstate(val, oldVal) {
				_self.handleOeder(val)
			}
		},
	}
</script>
<style lang="scss" scoped>
	.myOrder {
		background-color: #f7f5f6;

		.allOrder_container {
			.allOrder_item {
				background-color: #fff;
				margin-top: 15rpx;

				.allOrder_title {
					display: flex;
					justify-content: space-between;
					padding: 20rpx;
					border-bottom: 1rpx solid #eee;

					.allOrder_title_left {
						display: flex;
						font-weight: 600;
						align-items: center;

						text {
							display: block;
							background-color: $color;
							color: #fff;
							width: 65rpx;
							height: 32rpx;
							text-align: center;
							line-height: 32rpx;
							font-size: 24rpx;
							border-radius: 5rpx;
							margin-right: 10rpx;
						}
					}

					.allOrder_title_right {
						color: #999;
					}
					.jiesuan_warp {
						display: flex;
						.jiesuan {
							color: #FF3C36;
						}
						.delete {
							margin-right: 20rpx;
						}
					}
				}

				.allOrder_list {
					.allOrder_item {
						display: flex;
						border-bottom: 1rpx solid #eee;
						padding: 20rpx;

						.allOrder_item_icon {
							// flex: 1;
							width: 60rpx;
							margin-top: 20rpx;

							image {
								width: 60rpx;
								height: 60rpx;
							}
						}

						.allOrder_item_wrap {
							flex: 3;

							.allOrder_item_info {

								color: $color;
								font-weight: 600;
								margin-left: 10rpx;
								display: flex;
								justify-content: space-between;
								margin-top: 20rpx;

								.qiu_text {
									width: 400rpx;
									display: flex;
									flex-wrap: wrap;
								}

								.allOrder_item_qishu {
									flex: 1;
									font-size: 24rpx;
									text-align: center;

									.qishu_text {
										color: #666;

										text {
											margin-left: 20rpx;
										}
									}

									.qishu_info {
										width: 90rpx;
										height: 32rpx;
										color: #333;
										background-color: #f7f5f6;
										margin-left: 130rpx;
										margin-top: 20rpx;
										line-height: 32rpx;
									}
								}

								text {
									margin-left: 15rpx;
								}

								.blue {
									color: blue;
								}
							}
						}



					}

					.total_wrap {
						display: flex;
						justify-content: space-between;

						padding: 20rpx 30rpx;

						.total_time {
							color: #999;
							font-size: 24rpx;
						}
					}
				}
			}
		}
	}

	.tankuang {
		position: fixed;
		top: 50%;
		left: 50%;
		background-color: #fff;
		box-shadow: 0 2px 4px #000;
		width: 60%;
		height: auto;
		border-radius: 10rpx;
		margin-left: -30%;
		margin-top: -40%;

		view {
			width: 100%;
			height: 80rpx;
			line-height: 80rpx;
			text-align: center;
			color: #FB471E;
			font-size: 16px;
			border-bottom: 1px solid #eee;

			&:last-child {
				border-bottom: none;
			}
		}
	}

	.tabs {
		background-color: #fff;

		.tabs_wrap {
			display: flex;
			height: 60rpx;
			align-items: center;
			justify-content: space-between;
			padding: 30rpx 20rpx;

			.tabs_item {
				border-bottom: 6rpx solid $color;
				padding-bottom: 15rpx;
				font-size: 32rpx;
			}
		}
	}

	.total_right text {
		font-size: 24rpx;
		color: #999999;
	}
</style>
